@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body {
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  display: flex;
}

.card__wrap {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.card__wrap--margin {
  margin: 17px 0 22px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__text {
  flex-direction: column;
}

.card__title {
  color: #060b35;
  margin: 40px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin: 4px 0 0 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__stars {
  margin-left: 16px;
  display: flex;
}

.card__stars-img {
  background: url("star-active.edda4c43.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.card__stars-img:last-child {
  background: url("star.822d561e.svg") center no-repeat;
  margin-right: 0;
}

.card__number-reviews {
  color: #060b35;
  margin: 0 16px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-text {
  color: #616070;
  margin: 0 0 16px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-numbr {
  color: #060b35;
  margin: 0 16px 16px 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button--hover:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

